import { Keybtns } from "./Keybtns";
import { data } from "./data";

window.onload = function () {
  let lang = "en";

  let wrapper = document.createElement("div");
  wrapper.className = "wrapper";
  document.body.append(wrapper);

  let title = document.createElement("h1");
  title.innerHTML = "Virtual Keyboard";
  document.body.prepend(title);

  let textarea = document.createElement("textarea");
  textarea.setAttribute("autofocus", "");
  title.after(textarea);

  let description = document.createElement("h2");
  description.innerHTML =
    "The keyboard was created in the Windows operating system";
  document.body.append(description);

  let changeLangDesc = document.createElement("h2");
  changeLangDesc.innerHTML =
    "To switch the language combination: left CtrlLeft + AltLeft";
  document.body.append(changeLangDesc);

  const renderKeysToDom = () => {
    let keyboardWrapper = getKeyboardWrapper();

    for (let i = 0; i < 5; i++) {
      let row = document.createElement("div");
      row.className = "row";
      keyboardWrapper.append(row);

      generateKeys(data[i]).forEach((key) => {
        row.append(key.generateKey());
      });
    }

    wrapper.append(keyboardWrapper);
  };

  const generateKeys = (data) => {
    let keys = [];
    data.forEach((key) => {
      keys.push(new Keybtns(key));
    });

    return keys;
  };

  const getKeyboardWrapper = () => {
    const keyboardContainer = document.createElement("div");
    keyboardContainer.className = "container";
    keyboardContainer.innerHTML = "";
    document.body.append(keyboardContainer);
    return keyboardContainer;
  };
  if (data) {
    renderKeysToDom();
  }

  const textArea = document.querySelector("textarea");
  textArea.innerHTML = "Enter something...";

  document.addEventListener("keydown", (e) => {
    if (e.key === "Tab") {
      e.preventDefault();
      textArea.setRangeText(
        "\t",
        textArea.selectionStart,
        textArea.selectionEnd,
        "end"
      );
    }

    if (e.key === "Alt") {
      e.preventDefault();
    }

    let pressed = document.querySelector(`.${e.code}`);
    pressed.classList.add("pressed");
  });

  document.addEventListener("keyup", (e) => {
    //console.log("keyup", e.code);
    let unpressed = document.querySelector(`.${e.code}`);
    unpressed.classList.remove("pressed");
  });

  document.addEventListener("mousedown", (e) => {
    if (e.target.tagName === "SPAN") {
      e.preventDefault();
    }
    if (
      e.target.tagName !== "SPAN" ||
      e.target.innerHTML === "Ctrl" ||
      e.target.innerHTML === "Alt" ||
      e.target.innerHTML === "Win"
    ) {
      return;
    }
    if (e.target.innerHTML === "TAB") {
      e.preventDefault();
      textArea.setRangeText(
        "\t",
        textArea.selectionStart,
        textArea.selectionEnd,
        "end"
      );
    }
    if (e.target.innerHTML === "Backspace") {
      if (textArea.selectionStart == 0) {
        return;
      }
      e.preventDefault();
      textArea.setRangeText(
        "",
        textArea.selectionStart - 1,
        textArea.selectionEnd,
        "start"
      );
      return;
    }
    if (e.target.innerHTML === "DEL") {
      e.preventDefault();
      textArea.setRangeText(
        "",
        textArea.selectionStart,
        textArea.selectionEnd + 1,
        "start"
      );
      return;
    }
    if (e.target.innerHTML === "Space") {
      e.preventDefault();
      textArea.setRangeText(
        " ",
        textArea.selectionStart,
        textArea.selectionEnd,
        "end"
      );
      return;
    }
    if (e.target.innerHTML === "Enter") {
      e.preventDefault();
      textArea.setRangeText(
        "\n",
        textArea.selectionStart,
        textArea.selectionEnd,
        "end"
      );
      return;
    }

    if (e.target.innerHTML === "CapsLock") {
      if (lang == "en") {
        const enKeys = document.querySelectorAll(".eng");
        for (let key of enKeys) {
          key.querySelector(".caseDown").classList.toggle("hidden");
          key.querySelector(".caps").classList.toggle("hidden");
        }
      } else {
        const enKeys = document.querySelectorAll(".rus");
        for (let key of enKeys) {
          key.querySelector(".caseDown").classList.toggle("hidden");
          key.querySelector(".caps").classList.toggle("hidden");
        }
      }
      return;
    }

    if (e.target.innerHTML === "Shift") {
      //console.log(e.target);

      if (lang == "en") {
        const enKeys = document.querySelectorAll(".eng");

        for (let key of enKeys) {
          if (key.querySelector(".caps").className === "caps") {
            key.querySelector(".caseDown").className = "caseDown hidden";
            key.querySelector(".caseUp").className = "caseUp hidden";
            key.querySelector(".caps").className = "caps hidden";
            key.querySelector(".shiftCaps").className = "shiftCaps";
          } else {
            key.querySelector(".caseDown").classList.toggle("hidden");
            key.querySelector(".caseUp").classList.toggle("hidden");
          }
        }
      } else {
        const enKeys = document.querySelectorAll(".rus");
        for (let key of enKeys) {
          if (key.querySelector(".caps").className === "caps") {
            key.querySelector(".caseDown").className = "caseDown hidden";
            key.querySelector(".caseUp").className = "caseUp hidden";
            key.querySelector(".caps").className = "caps hidden";
            key.querySelector(".shiftCaps").className = "shiftCaps";
          } else {
            key.querySelector(".caseDown").classList.toggle("hidden");
            key.querySelector(".caseUp").classList.toggle("hidden");
          }
        }
      }
    }
    return;

    textArea.setRangeText(
      e.target.innerHTML,
      textArea.selectionStart,
      textArea.selectionEnd,
      "end"
    );
    //console.log(e.target.innerHTML);
  });

  document.addEventListener("mouseup", function (e) {
    if (e.target.innerHTML === "Shift") {
      //console.log(e.target);
      if (lang == "en") {
        const enKeys = document.querySelectorAll(".eng");
        for (let key of enKeys) {
          if (key.querySelector(".caps").className === "caps hidden") {
            key.querySelector(".caseDown").className = "caseDown hidden";
            key.querySelector(".caseUp").className = "caseUp hidden";
            key.querySelector(".caps").className = "caps";
            key.querySelector(".shiftCaps").className = "shiftCaps hidden";
          } else {
            key.querySelector(".caseDown").className.toggle("caseDown hidden");
            key.querySelector(".caseUp").className.toggle("caseUp hidden");
          }
        }
      } else {
        const enKeys = document.querySelectorAll(".rus");
        for (let key of enKeys) {
          if (key.querySelector(".caps").className === "caps hidden") {
            key.querySelector(".caseDown").className = "caseDown hidden";
            key.querySelector(".caseUp").className = "caseUp hidden";
            key.querySelector(".caps").className = "caps";
            key.querySelector(".shiftCaps").className = "shiftCaps hidden";
          } else {
            key.querySelector(".caseDown").className.toggle("caseDown hidden");
            key.querySelector(".caseUp").className.toggle("caseUp hidden");
          }
        }
      }
      return;
    }
  });
  //Checking keyboard shortcuts CtrlLeft + AltLeft
  function runOnKeys(func, ...codes) {
    let pressed = new Set();

    document.addEventListener("keydown", function (e) {
      pressed.add(e.code);

      for (let code of codes) {
        if (!pressed.has(code)) {
          return;
        }
      }

      pressed.clear();

      func();
    });

    document.addEventListener("keyup", function (e) {
      pressed.delete(e.code);

      if (e.target.innerHTML === "Shift") {
        console.log(e.target);
        if (lang == "en") {
          const enKeys = document.querySelectorAll(".eng");
          for (let key of enKeys) {
            key.querySelector(".caseUp").classList.toggle("hidden");
            key.querySelector(".caseDown").classList.toggle("hidden");
          }
        } else {
          const enKeys = document.querySelectorAll(".rus");
          for (let key of enKeys) {
            key.querySelector(".caseUp").classList.toggle("hidden");
            key.querySelector(".caseDown").classList.toggle("hidden");
          }
        }
        return;
      }
    });
  }

  function setLocalStorage() {
    localStorage.setItem("lang", lang);
  }

  function getLocalStorage() {
    console.log("getLocalStorage");
    if (localStorage.getItem("lang")) {
      console.log("ku");
      lang = localStorage.getItem("lang");
    } else {
      console.log("или тут");
      lang = "en";
    }
  }
  window.addEventListener("load", getLocalStorage);

  function changeLang() {
    console.log("Change Language");
    const keys = document.querySelectorAll(".key");
    console.log(lang);
    if (lang === "en") {
      lang = true;
    } else {
      lang = false;
    }
    console.log(lang);
    for (let key of keys) {
      let spanRUS = key.querySelector(".rus");
      let spanENG = key.querySelector(".eng");

      spanRUS.classList.toggle("hidden");
      spanENG.classList.toggle("hidden");

      if (lang) {
        //Т.е. переводим с английского на русский
        console.log("мы тут");
        spanRUS.querySelector(".caseDown").className = spanENG.querySelector(
          ".caseDown"
        ).className;
        spanRUS.querySelector(".caseUp").className = spanENG.querySelector(
          ".caseUp"
        ).className;
        spanRUS.querySelector(".caps").className = spanENG.querySelector(
          ".caps"
        ).className;
        spanRUS.querySelector(".shiftCaps").className = spanENG.querySelector(
          ".shiftCaps"
        ).className;
      } else {
        spanENG.querySelector(".caseDown").className = spanRUS.querySelector(
          ".caseDown"
        ).className;
        spanENG.querySelector(".caseUp").className = spanRUS.querySelector(
          ".caseUp"
        ).className;
        spanENG.querySelector(".caps").className = spanRUS.querySelector(
          ".caps"
        ).className;
        spanENG.querySelector(".shiftCaps").className = spanRUS.querySelector(
          ".shiftCaps"
        ).className;
      }
    }

    lang = !lang;

    if (lang === true) {
      lang = "en";
    } else {
      lang = "ru";
    }

    localStorage.setItem("lang", lang);
  }

  runOnKeys(changeLang, "ControlLeft", "AltLeft");
};
